<template>
  <div class="runner">
    <Login
      :logo-url="workspaceStore.state.workspace?.logoUrl ?? undefined"
      :brand-name="workspaceStore.state.workspace?.brandName"
      :locale="workspaceStore.state.workspace?.language ?? 'en'"
      @done="done"
    />
  </div>
</template>

<script lang="ts" setup>
import Login from '@/apps/player/auth/Login.vue';
import { useRoute, useRouter } from 'vue-router';
import { useWorkspaceStore } from '../store/workspaceStore';

const router = useRouter();
const route = useRoute();

const workspaceStore = useWorkspaceStore();

const done = async () => {
  const { redirect, ...query } = route.query;

  if (redirect) {
    await router.push({
      path: redirect as string,
      query,
      params: route.params,
    });
    return;
  }

  router.push({
    name: 'playerHome',
    query,
  });
};
</script>

<style lang="scss" scoped>
@import '@/styles/index.scss';

.runner {
  width: 100%;
  height: 100%;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
